
$marginFrom: -5; 
$marginTo: 5;

@each $name, $dir, $step in 
(m, "", 2),
(m, "", 5),
(ml, "-left", 2),
(ml, "-left", 5),
(mr, "-right", 2),
(mr, "-right", 5),
(mt, "-top", 2),
(mt, "-top", 5),
(mb, "-bottom", 2),
(mb, "-bottom", 5),
{
    @for $i from $marginFrom through $marginTo {
        .#{$name}-#{$i * $step} {
            margin#{$dir}: ($i * $step) + px;
        }
    }
}


@each $name, $dir, $step in 
(m, "", 2),
(m, "", 5),
(ml, "-left", 2),
(ml, "-left", 5),
(mr, "-right", 2),
(mr, "-right", 5),
(mt, "-top", 2),
(mt, "-top", 5),
(mb, "-bottom", 2),
(mb, "-bottom", 5),
{
    @for $i from $marginFrom through $marginTo {
        .#{$name}-#{$i * $step}-it {
            margin#{$dir}: ($i * $step) + px !important;
        }
    }
}